.modal-header {
    padding: 5px 15px;
    background-color: #333;
}

.modal-title {
    font-size: 18px;
    color: #fff;
}

.actionBtnContainer {
    display: flex;
    align-items: center;
}

.actionBtnContainer > button {
    border: 0;
    margin: 0 5px;
    font-size: 14px;
}

.actionBtnContainer > button span {
    border: 0;
    margin: 0 5px;
    font-size: 14px;
}